.skills-container-a {
    height: 850px; /* Mucho contenido para permitir scroll */
    width: 50%;
    overflow-y: scroll; /* Permitir desplazamiento vertical */
    /* Ocultar la barra de scroll */
    -ms-overflow-style: none;  /* Internet Explorer 10+ */
    scrollbar-width: none;  /* Firefox */
}

.skills-container-a::-webkit-scrollbar {
    display: none;  /* Para Chrome, Safari y Opera */
}

.bulto{
    height: 200vh;
}
.skills-container {
    height: 100%;
    margin-bottom: 200vh;
    position: fixed;
    top: 6.5%;
    left: 35%;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 50px;
    perspective: 1000px;
    z-index: 1;
}

.progress-line {
    display: flex;
    position: relative;
    top: -118px;
    height: 110%;
    width: 293px;
    background-color: #ddd;
    transform: rotateX(60deg);
    justify-content: center;
    overflow: hidden;
    flex-wrap: nowrap;
    align-content: center;
}

.container-line-three{
    position: relative;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: space-around;
    transform: translateY(-1000px); 
    left: 17px;
    
}


.container-line-two{
    position: relative;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: space-around;
    transform: translateY(-1000px); 
    left: 7px;
    
}


.container-line-one{
    position: relative;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: space-around;
    transform: translateY(-1000px);
    left: -3px;
}




.line {
    
    height: 50px;
    width: 2%;
    bottom: 0;
    
    transition: height 0.3s ease;
    transform: scale(1) translateX(-2px) translateY(-9px);
    border: solid black; 
    border-width: 5px;
}

.dot {
    position: absolute;
    height: 20px;
    width: 20px;
    background-color: #fff;
    border: 3px solid #0d6efd;
    border-radius: 50%;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3); /* Sombra suave */
}

#dot1 {
    top: 20px;
}

#dot2 {
    top: 160px;
}

#dot3 {
    top: 280px;
}

.skills {
    display: flex;
    flex-direction: column;
    gap: 27px;
    transform: rotateX(0deg) rotateY(357deg) rotateX(39deg);
    position: relative;
    top: -62px;
    height: 100%;
    left: -36px;
}

.skill h3 {
    margin-bottom: 10px;
    color: #0d6efd;
    font-weight: bold; /* Hacer el texto más destacado */
}
.skill {
    background-color: #fff;
    border-radius: 10px;
    padding: 20px;
    width: 200px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.4); /* Sombra más intensa */
    opacity: 0;
    transform: translateX(-40px) translateZ(0) rotateY(5deg) rotateX(2deg); /* Suave efecto 3D */
    transform-origin: center; /* Cambiar el origen para centrado */
    transition: opacity 0.3s ease, transform 0.6s ease; /* Transición suave */
}

.skill.active {
    opacity: 1;
    transform: translateX(0) scale(1.02) rotateY(0deg) rotateX(0deg) rotate(45deg);
    transition: opacity 0.3s ease, transform 0.6s ease;
}

/* Añadir un hover effect para mejorar la interacción */
.skill:hover {
    box-shadow: 0 8px 25px rgba(0, 0, 0, 0.5);
    transform: translateX(0) scale(1.50) rotateY(0deg) rotateZ(0deg) translate(32%, -28%);
    top: 50%;
    left: 50%;
    cursor: pointer;
}

.car {
    height: 50px;
    width: 50px;
    position: relative;
    top: 234px;
    z-index: 1;
    
    transform: rotateX(45deg) rotateY(5deg) translateZ(-5px);
    filter: drop-shadow(5px 5px 10px rgba(0, 0, 0, 0.5)); /* Sombra siguiendo la forma del coche */
    transition: transform 0.5s ease, filter 0.5s ease;
}

